<template>
	<div id="app" style="position: relative;">
		<!--toubu-->
		<!--<mt-header fixed title="MyApp"></mt-header>-->
		 	<div class="app_nav">
		    	<span v-for="(i,$index) in arr" :class="type==$index?'active':''" @click="type=$index">
		    		<a :href="i.path">{{i.title}}</a>
		    		
		    	</span>
		 </div>
		<!--neirong-->
		<div id="center" style="width: 100%;min-height:40rem;background: #CCCCC;padding-top: 60px;">
		
			<keep-alive>
				<router-view></router-view>
			</keep-alive>
		</div>
		<!--dibu-->
		<tabbar></tabbar>
	</div>
</template>

<script>
	import tabbar from './tabbar.vue'
	import { Indicator } from 'mint-ui';
	export default {
		name: 'app',
		components: {
			tabbar
		},
		data() {
			return {
				type:0,
			     arr:[
			     		{path:'#/indexvue',title:'推荐'},
			     		{path:'#/img',title:'图片'},
			     		{path:'#/videos',title:'视频'},
			     		{path:'#/fonts',title:'文字'},
			     ]
			}
		},
		created(){
			this.loading()
		},
		mounted(){
			setTimeout(()=>{
					Indicator.close();
				},1000)
		},
		methods:{
			loading(){
				
			}
		},
		watch: {
			'$route.path' () {
				  this.loading()
				  setTimeout(()=>{
					Indicator.close();
				},1000)
			}
		}
	}
</script>

<style>
	* {
		margin: 0;
		padding: 0;
		text-decoration: none;
	}
	.app_nav{
		width:100%;
		height:0.5rem;
		position: fixed;
		top:0;
		left: 0;
		background: white;
	}
	
	.app_nav span{
		padding:0 0.3rem;
		line-height: 0.5rem;
	}
	
	.app_nav span a{
		color: black;
		font-size:0.24rem;
	}
	.app_nav span.active{
		line-height: 0.5rem;
	}
	.app_nav span.active a{
		color: pink;
		font-size: 0.18rem;
	}
</style>


