<template>
	<mt-tabbar v-model="selected" class="ok">
			<mt-tab-item id="#/">
				<div class="fotter">
					<span class="iconfont" style="font-size: 16px;">&#xe517;</span>
				</div>
				 <div class="zi" >
				 	首页
				 </div>

			</mt-tab-item>
			<mt-tab-item id="#/b">
				<div class="fotter">
					<span class="iconfont" style="font-size: 16px;">&#xe601;</span>
				</div>
				 <div class="zi" >
				 发现
				 </div>
			   </mt-tab-item>
			   <mt-tab-item id="#/f">
				<div class="fottesr" style="width:35px;height:35px;background: deeppink;text-align: center;font-size: 36px;color: white;border-radius:50%;line-height: 35px;margin-left:auto;margin-right: auto;;">
					+
				</div>
			   </mt-tab-item>
			<mt-tab-item id="#/c">
				<div class="fotter">
					<span class="iconfont" style="font-size: 16px;">&#xe515;</span>
				</div>
				 <div class="zi" >
				消息
				</div>

			</mt-tab-item>
			<mt-tab-item id="#/d">
				<div class="fotter">
				<span class="iconfont" style="font-size:16px;">&#xe611;</span>
				</div>
				 <div class="zi">
				我的
				</div>

				
			</mt-tab-item>
				
	</mt-tabbar>
</template>

<script>
	export default {
		data() {
			return {
				selected: '#/'
			}
		},
		created(){
			this.selected = '#'+this.$route.path
		},
		watch: {
			selected(a) {
				location.href = a
			}
		}
	}
</script>

<style>
	*{
		margin: 0;
		padding: 0;
	}
.mint-tabbar{
 	width: 100%;
 	height: 50px;
 	position: fixed;
 	bottom:0;
 }
 .zi{
 	line-height: 20px;
 	font-size: 14px;
 }
</style>


