import Vue from 'vue'
import Router from 'vue-router'
import Resource from 'vue-resource'
import b from './b.vue'
import c from './c.vue'
import d from './d.vue'
import f from './f.vue'

import indexvue from './routes/indexvue.vue'
import img from './routes/img.vue'
import videos from './routes/videos.vue'
import fonts from './routes/fonts.vue'
import news from './routes/news.vue'

Vue.use(Resource)
Vue.use(Router)
export default new Router({
	routes:[
		{path:'/',name:'indexvue',component:indexvue},
	   {
	  	path:'/b',
	  	name:'b',
	  	component:b
	  },
	   {
	  	path:'/c',
	  	name:'c',
	  	component:c
	  },
	   {
	  	path:'/d',
	  	name:'d',
	  	component:d
	  },
	  {
	  	path:'/f',
	  	name:'f',
	  	component:f
	  },
	{path:'/img',name:'img',component:img},
	{path:'/videos',name:'videos',component:videos},
	{path:'/fonts',name:'fonts',component:fonts},
	]
})






