<template>
	<div>
		<div class="img_item" v-for="i in fontlist">
			<div class="img_header">
				<img :src="i.header" alt="" style="width:35px;height:35px;border-radius: 50%;">
				<span style="font-size: 12px;margin-left: 15px;vertical-align: top;display: inline-block;margin-top:11px;">{{i.username}}</span>
			</div>
			<div style="min-height: 50px;padding:0 20px;box-sizing: border-box;">
				<p style="font-size: 14px;line-height: 20px;">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{i.text}}</p>
			</div>
			<div class="foot" style="height: 50px;">
				<span style="width:24% ;text-align: center;display: inline-block;line-height: 50px;"><i class="iconfont" style="line-height: 50px;font-size: 18px;">&#xe602;</i>&nbsp;&nbsp;{{i.up}}</span>
				<span style="width:24% ;text-align: center;display: inline-block;line-height: 50px;"><i class="iconfont" style="line-height: 50px;font-size: 18px;">&#xe646;</i>&nbsp;&nbsp;{{i.down}}</span>
				<span style="width:24% ;text-align: center;display: inline-block;line-height: 50px;"><i class="iconfont" style="line-height: 50px;font-size: 18px;">&#xe707;</i>&nbsp;&nbsp;{{i.comment}}</span>
				<span style="width:24% ;text-align: center;display: inline-block;line-height: 50px;"><i class="iconfont" style="line-height: 50px;font-size: 18px;">&#xe61c;</i>&nbsp;&nbsp;{{i.forward}}</span>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
	  	data () {
	    return {
	    	fontlist:[]
	    }
  },
  created(){
  	this.$http.get('https://www.apiopen.top/satinGodApi?type=2&page=1').then((data)=>{
  		console.log(data.body.data)
  		this.fontlist=data.body.data
  	})
  }
}
</script>

<style>
	.img_item{
		min-height: 100px;
		padding-bottom: 20px;
	}
	.img_header{
		height:35px;
		width: 100%;
		padding:0 10px;
		box-sizing: border-box;
	}
</style>


